import React from "react";

import "./InvoiceStyle.css";

import arrowLeft from "../assets/arrow-left.svg";
import arrowRight from "../assets/arrow-right.svg";
import tickIcon from "../assets/tick.svg";
import themeImage from "../assets/theme.svg";
import untamedNight from "../assets/untamed-night.png";
import aquaTheme from "../assets/aqua-theme.png";

export default function InvoiceStyle({
  handleInputChange,
  themeChange,
  brandColor,
  fontFamily,
}) {
  const colors = [
    "rgb(246, 77, 91)",
    "rgb(255, 168, 50)",
    "rgb(111, 30, 255)",
    "#000",
  ];
  return (
    <section className="Invoice-Style">
      <header>
        <h2>Invoice Style</h2>
        <h4>Choose Invoice Theme</h4>
      </header>
      <div className="format">
        <label>
          {themeChange !== "Invoice" && themeChange !== "Aqua Theme" && (
            <>
              <img src={arrowLeft} alt="icon" />
              <input
                type="checkbox"
                name="themeChange"
                value="Invoice"
                onChange={handleInputChange}
              />
            </>
          )}

          {themeChange === "Aqua Theme" && (
            <>
              <img src={arrowLeft} alt="icon" />
              <input
                type="checkbox"
                name="themeChange"
                value="Untamed Night"
                onChange={handleInputChange}
              />
            </>
          )}
        </label>
        <figure>
          {themeChange === "Invoice" && <img src={themeImage} alt="icon" />}
          {themeChange === "Untamed Night" && (
            <img src={untamedNight} alt="icon" />
          )}
          {themeChange === "Aqua Theme" && <img src={aquaTheme} alt="icon" />}
        </figure>{" "}
        <label>
          {((themeChange !== "Untamed Night" && themeChange === "Invoice") ||
            (themeChange === "Untamed Night" &&
              themeChange !== "Aqua Theme")) && (
            <img src={arrowRight} alt="icon" />
          )}
          {themeChange === "Invoice" && (
            <input
              type="checkbox"
              name="themeChange"
              value="Untamed Night"
              onChange={handleInputChange}
            />
          )}
          {themeChange === "Untamed Night" && (
            <input
              type="checkbox"
              name="themeChange"
              value="Aqua Theme"
              onChange={handleInputChange}
            />
          )}
        </label>
      </div>
      <div className="color">
        <h4>Choose a theme color</h4>
        <ul>
          {colors.map((color, i) => {
            console.log(color);
            return (
              <li>
                <span style={{ backgroundColor: color }}></span>
                {brandColor === color && <img src={tickIcon} alt="tick" />}
                <input
                  type="checkbox"
                  name="brandColor"
                  value={color}
                  onChange={handleInputChange}
                />
              </li>
            );
          })}
        </ul>
      </div>
      <footer>
        <h4>Choose a font</h4>
        <select
          defaultValue={fontFamily}
          name="fontFamily"
          onChange={handleInputChange}
        >
          <option value="Roboto, Helvetica, sans-serif">Default Font</option>
          <option value="Arial, Helvetica, sans-serif">Arial</option>
          <option value={`"Arial Black", Gadget`}>Arial Black</option>
          <option value={`"Comic Sans MS", cursive`}>Comic Sans MS</option>
          <option value={`"Courier New", Courier, monospace`}>
            Courier New
          </option>
          <option value={`Georgia, serif`}>Georgia</option>
          <option value={`"Palatino Linotype", "Book Antiqua", Palatino`}>
            Palatino Linotype
          </option>
          <option value={`"Times New Roman", Times`}>Times New Roman</option>
          <option value={`Impact, Charcoal`}>Impact</option>
          <option value={`"Lucida Sans Unicode", "Lucida Grande"`}>
            Lucida Sans Unicode
          </option>
          <option value={`Tahoma, Geneva`}>Tahoma, Geneva</option>
          <option value={`"Trebuchet MS", Helvetica`}>Trebuchet MS</option>
          <option value={`Verdana, Geneva`}>Verdana</option>
          <option value={`"Lucida Console", Monaco`}>
            "Lucida Console", Monaco
          </option>
        </select>
      </footer>
    </section>
  );
}
