import React, { useState } from "react";
import useForm from "../customHooks";
import moment from "moment";
import Invoice from "./Invoices/Invoice";
import jsPDF from "jspdf";
import html2canvas from "html2canvas";
import InvoiceStyle from "./InvoiceStyle/InvoiceStyle";
import InvoiceForm from "./InvoiceForm";
import UntamedNight from "./Invoices/UntamedNight/UntamedNight";

import "./Dashboard.css";
import AquaTheme from "./Invoices/AquaTheme/AquaTheme";

export default function Dashboard() {
  const [printPreview, setPrintPreview] = useState(false);
  const [items, setItems] = useState([]);
  const [fileUpload, setFileUpload] = useState(null);

  const invoiceName = `INVOICE${moment().format("DDMMYYYY")}${
    Math.floor(Math.random() * 100) + 10
  }`;

  const printDocument = () => {
    const input = document.getElementById("divToPrint");
    html2canvas(input, {
      allowTaint: true,
      taintTest: false,
      useCORS: true,
      scale: 1,
    })
      .then((canvas) => {
        const imgData = canvas.toDataURL("image/png");
        const pdf = new jsPDF();
        pdf.addImage(imgData, "JPEG", 0, 0);
        // pdf.output('dataurlnewwindow');
        pdf.save(`${invoiceName}.pdf`);
      })
      .then(() => {
        sessionStorage.clear();
        window.location.assign(
          "https://turbz.github.io/taevo-invoice-generator"
        );
      });
  };

  const storedVat = sessionStorage.getItem("vat");

  const togglePrintPreview = () => {
    setPrintPreview(!printPreview);
  };

  // const handleFileUpload = (event) => {
  //   setFileUpload({ file: URL.createObjectURL(event.target.files[0]) });
  // };

  const handleAddItem = (event) => {
    event.preventDefault();
    setItems([
      ...items,
      {
        id: (items.length += 1),
        description: inputs.description,
        rate: Number(inputs.rate).toFixed(2),
        units: Number(inputs.units).toFixed(0),
      },
    ]);
    inputs.description = "";
    inputs.rate = 0;
    inputs.units = 1;
  };

  const handleRemoveItem = (id) => {
    setItems(() => {
      return items.filter((i) => i.id !== id);
    });
  };

  const submitInvoice = () => {};

  const { inputs, handleInputChange, handleSubmit } = useForm(
    {
      brandColor: null,
      fontFamily: null,
      themeChange: "Invoice",
      clientName: "",
      dateIssued: "",
      invoiceNo: `#${moment().format("DDMMYYYY")}${
        Math.floor(Math.random() * 1000) + 100
      }`,
      yourName: "",
      yourAddress: "",
      yourTown: "",
      yourCity: "",
      postalCode: "",
      taxNumber: "",
      bankName: "",
      accountNumber: "",
      branchCode: "",
      dateDue: null,
      description: "",
      email: "",
      phone: "",
      priceType: "Units",
      website: "",
      vatAmount: 0,
      clientVATNumber: null,
      clientAddress: "",
      clientTown: "",
      clientCity: "",
      clientPostalCode: "",
      discountAmount: 0,
      rate: 0,
      units: 1,
      message: "success",
    },
    submitInvoice
  );

  const totalAmountDue = items.reduce((total, item) => {
    let totalAmount = item.rate * item.units;
    return total + totalAmount;
  }, 0);

  let totalAmountVatInclusive = totalAmountDue + totalAmountDue + storedVat;

  const discountAmountZero = () => {
    inputs.discountAmount = 0;
  };

  const file = fileUpload && fileUpload.file;

  const invoiceData = {
    clientName: inputs.clientName,
    dateIssued: inputs.dateIssued,
    invoiceNo: inputs.invoiceNo,
    yourName: inputs.yourName,
    yourAddress: inputs.yourAddress,
    yourTown: inputs.yourTown,
    yourCity: inputs.yourCity,
    taxNumber: inputs.taxNumber,
    bankName: inputs.bankName,
    accountNumber: inputs.accountNumber,
    branchCode: inputs.branchCode,
    dateDue: inputs.dateDue,
    email: inputs.email,
    phone: inputs.phone,
    website: inputs.website,
    brandColor: inputs.brandColor,
    fontFamily: inputs.fontFamily,
    postalCode: inputs.postalCode,
    avatar: file,
    invoiceClass: "Print-Invoice",
    discountAmount: inputs.discountAmount,
    items: items,
    priceType: inputs.priceType,
    clientTaxNumber: inputs.clientTaxNumber,
    clientAddress: inputs.clientAddress,
    clientTown: inputs.clientTown,
    clientCity: inputs.clientCity,
    clientPostalCode: inputs.clientPostalCode,
    // saveForm:saveForm,
    togglePrintPreview: togglePrintPreview,
    totalAmountVatInclusive: totalAmountVatInclusive,
    totalAmountDue: totalAmountDue,
  };

  return (
    <>
      {!printPreview ? (
        <section className="Dashboard">
          <header>
            <h2>
              Create an invoice<span>Beta</span>
            </h2>
            <button onClick={togglePrintPreview}>Print Preview</button>
          </header>

          <div>
            <InvoiceForm
              clientName={inputs.clientName}
              dateIssued={inputs.dateIssued}
              invoiceNo={inputs.invoiceNo}
              yourName={inputs.yourName}
              yourAddress={inputs.yourAddress}
              yourTown={inputs.yourTown}
              yourCity={inputs.yourCity}
              taxNumber={inputs.taxNumber}
              accountNumber={inputs.accountNumber}
              branchCode={inputs.branchCode}
              dateDue={inputs.dateDue}
              email={inputs.email}
              phone={inputs.phone}
              website={inputs.website}
              postalCode={inputs.postalCode}
              handleInputChange={handleInputChange}
              avatar={file}
              discountAmount={inputs.discountAmount}
              items={items}
              addItem={handleAddItem}
              handleAddItem={handleAddItem}
              description={inputs.description}
              handleRemoveItem={handleRemoveItem}
              rate={inputs.rate}
              units={inputs.units}
              handleSubmit={handleSubmit}
              vatAmount={inputs.vatAmount}
              priceType={inputs.priceType}
              discountAmountZero={discountAmountZero}
              totalAmountDue={totalAmountDue}
              clientTaxNumber={inputs.clientTaxNumber}
              clientAddress={inputs.clientAddress}
              clientTown={inputs.clientTown}
              clientCity={inputs.clientCity}
              clientPostalCode={inputs.clientPostalCode}
            />
            <InvoiceStyle
              handleInputChange={handleInputChange}
              themeChange={inputs.themeChange}
              brandColor={inputs.brandColor}
              fontFamily={inputs.fontFamily}
            />
          </div>
        </section>
      ) : (
        <>
          <div className="mb5 btn">
            {printPreview && (
              <>
                <button onClick={togglePrintPreview}>Edit</button>
                <button onClick={printDocument}>Print</button>
              </>
            )}
          </div>
          <div
            id="divToPrint"
            style={{
              position: "relative",
              width: "210mm",
              minHeight: "297mm",
              marginLeft: "auto",
              marginRight: "auto",
              backgroundColor: "#f2f6f3",
              // border: "2px solid black",
            }}
          >
            {inputs.themeChange === "Invoice" && <Invoice {...invoiceData} />}
            {inputs.themeChange === "Untamed Night" && (
              <UntamedNight {...invoiceData} />
            )}
            {inputs.themeChange === "Aqua Theme" && (
              <AquaTheme {...invoiceData} />
            )}
          </div>
        </>
      )}
    </>
  );
}
