import React, { useState } from "react";

import removeIcon from "./assets/close-icon.svg";
import trashIcon from "./assets/trash.svg";

export default function InvoiceForm({
  items,
  description,
  units,
  rate,
  discountAmount,
  clientName,
  dateIssued,
  vatAmount,
  totalAmountDue,
  discountAmountZero,
  addItem,
  bankName,
  taxNumber,
  yourName,
  email,
  phone,
  yourAddress,
  priceType,
  yourCity,
  yourTown,
  postalCode,
  branchCode,
  accountNumber,
  handleInputChange,
  handleAddItem,
  handleRemoveItem,
  clientTaxNumber,
  clientAddress,
  clientTown,
  clientCity,
  clientPostalCode,
}) {
  const [toggleVatPercentage, setVatPercentage] = useState(false);
  const [toggleDiscount, setToggleDiscount] = useState(false);

  if (totalAmountDue <= 0) {
    sessionStorage.removeItem("vat");
  }

  const onToggleVatPercentage = (event) => {
    setVatPercentage(!toggleVatPercentage);
    addItem(event);
    vatAmount = 15 / 100;
    sessionStorage.setItem("vat", vatAmount);
  };

  const storedVat = sessionStorage.getItem("vat");

  const removeVatAmount = (event) => {
    setVatPercentage(false);
    sessionStorage.removeItem("vat");
    addItem(event);
  };

  const removeDiscountAmount = (event) => {
    setToggleDiscount(false);
    discountAmountZero();
    addItem(event);
  };

  return (
    <form className="form">
      <div className="client">
        <h3>Client Information</h3>
        <div>
          <label>
            <span>Client Name</span>

            <input
              type="text"
              name="clientName"
              placeholder="Client Name"
              defaultValue={clientName}
              onChange={handleInputChange}
            />
          </label>
          <input
            type="text"
            name="clientAddress"
            placeholder="Client Address"
            defaultValue={clientAddress}
            onChange={handleInputChange}
          />
          <input
            type="text"
            name="clientTown"
            placeholder="Client Town"
            defaultValue={clientTown}
            onChange={handleInputChange}
          />
          <input
            type="text"
            name="clientCity"
            placeholder="Client City"
            defaultValue={clientCity}
            onChange={handleInputChange}
          />
          <input
            type="text"
            name="clientPostalCode"
            placeholder="Client Postal Code"
            defaultValue={clientPostalCode}
            onChange={handleInputChange}
          />
          <input
            type="text"
            name="clientTaxNumber"
            placeholder="Client VAT Reg. no"
            defaultValue={clientTaxNumber}
            onChange={handleInputChange}
          />
        </div>
      </div>
      <div className="company">
        <h3>Company Information</h3>
        <div>
          <input
            type="text"
            name="yourName"
            placeholder="Your Name"
            defaultValue={yourName}
            onChange={handleInputChange}
          />{" "}
          <input
            type="text"
            name="yourAddress"
            placeholder="Your Address"
            defaultValue={yourAddress}
            onChange={handleInputChange}
          />
          <input
            type="text"
            name="yourTown"
            placeholder="Your Town"
            defaultValue={yourTown}
            onChange={handleInputChange}
          />
          <input
            type="text"
            name="yourCity"
            placeholder="Your City"
            defaultValue={yourCity}
            onChange={handleInputChange}
          />
          <input
            type="text"
            name="postalCode"
            placeholder="Postal Code"
            defaultValue={postalCode}
            onChange={handleInputChange}
          />
          <input
            type="text"
            name="email"
            placeholder="email"
            defaultValue={email}
            onChange={handleInputChange}
          />
          <input
            type="text"
            name="phone"
            placeholder="phone"
            defaultValue={phone}
            onChange={handleInputChange}
          />
          <input
            type="text"
            name="taxNumber"
            placeholder="VAT Reg. no"
            defaultValue={taxNumber}
            onChange={handleInputChange}
          />
        </div>
      </div>

      <div className="banking-details">
        <h3>Bank Details</h3>
        <div>
          <input
            type="text"
            name="bankName"
            placeholder="Bank Name"
            defaultValue={bankName}
            onChange={handleInputChange}
          />
          <input
            type="text"
            name="accountNumber"
            placeholder="Account Number"
            defaultValue={accountNumber}
            onChange={handleInputChange}
          />
          <input
            type="text"
            name="branchCode"
            placeholder="Branch Code"
            defaultValue={branchCode}
            onChange={handleInputChange}
          />
        </div>
      </div>

      <div className="items">
        <h3>Items</h3>

        <ul>
          <li>Description</li>
          <li>Price</li>
          <li>
            <select
              name="priceType"
              defaultValue={priceType}
              onChange={handleInputChange}
            >
              <option value="Units">Units</option>
              <option value="Hours">Hours</option>
            </select>
          </li>
          <li>Subtotal</li>
        </ul>

        {items.map((item, i) => {
          return (
            item.description !== "" &&
            item.rate !== 0 &&
            item.units !== 0 && (
              <ul key={i}>
                <li>
                  <img
                    src={trashIcon}
                    alt="delete"
                    onClick={() => handleRemoveItem(item.id)}
                  />
                  <span>{item.description}</span>
                </li>
                <li>{item.rate}</li>
                <li>{item.units}</li>
                <li>{(item.rate * item.units).toFixed(2)}</li>
              </ul>
            )
          );
        })}

        <label className="add-item">
          <input
            type="text"
            name="description"
            placeholder="Item description"
            value={description}
            onChange={handleInputChange}
          />

          <input
            type="text"
            name="rate"
            placeholder="Price"
            value={rate}
            onChange={handleInputChange}
          />

          <input
            type="text"
            name="units"
            placeholder="Units"
            value={units}
            onChange={handleInputChange}
          />
          <button
            style={{
              backgroundColor:
                description !== "" && rate > 0
                  ? "#7fbcbe"
                  : "rgba(0, 0, 0, .1)",
            }}
            onClick={handleAddItem}
            disabled={(description === "" || rate < 1) && "disabled"}
          >
            Add
          </button>
        </label>
      </div>
      <div className="extra">
        {!toggleDiscount && discountAmount <= 0 ? (
          <button onClick={() => setToggleDiscount(!false)}>
            Add Discount
          </button>
        ) : (
          <label>
            <span>
              Discount <span>R</span>
            </span>
            <input
              type="text"
              name="discountAmount"
              defaultValue={discountAmount}
              onChange={handleInputChange}
            />

            <img
              src={removeIcon}
              alt="remove discount"
              onClick={removeDiscountAmount}
            />
          </label>
        )}
        {!toggleVatPercentage && storedVat <= 0 ? (
          <button
            style={{
              backgroundColor:
                totalAmountDue > 0 ? "#7fbcbe" : "rgba(0, 0, 0, .1)",
            }}
            onClick={onToggleVatPercentage}
            disabled={totalAmountDue <= 0 && "disabled"}
          >
            Include VAT
          </button>
        ) : (
          <label>
            <span>
              VAT @ 15% <span>R</span>
            </span>
            <input
              type="text"
              value={((totalAmountDue - discountAmount) * storedVat).toFixed(2)}
            />
            <img
              src={removeIcon}
              alt="remove discount"
              onClick={removeVatAmount}
            />
          </label>
        )}
      </div>
    </form>
  );
}
