import React from "react";
import moment from "moment";

import "./AquaTheme.css";

export default function AquaTheme({
  clientName,
  dateIssued,
  invoiceNo,
  yourName,
  yourAddress,
  yourTown,
  yourCity,
  postalCode,
  taxNumber,
  accountNumber,
  branchCode,
  dateDue,
  email,
  phone,
  website,
  discountAmount,
  items,
  avatar,
  totalAmountVatInclusive,
  totalAmountDue,
  toggleSaveForm,
  saveForm,
  invoiceClass,
  brandColor,
  fontFamily,
  bankName,
  priceType,
}) {
  const storedVat = sessionStorage.getItem("vat");
  return (
    <>
      <div style={{ fontFamily: fontFamily }} className="Aqua-Theme">
        <header>
          <h4 style={{ color: brandColor }}>Hello! This is your</h4>

          <h1>Invoice</h1>
          <h5>
            <span>NO</span> {invoiceNo} | {moment().format("DD MMM YYYY")}
          </h5>
          <p>
            <span>for</span>
            <span>{clientName}</span>
          </p>
        </header>

        <div className="details">
          <ul>
            <li>Description</li>
            <li>Rate</li>
            <li>{priceType}</li>
          </ul>
          <div className="items ">
            {items.map((item, i) => {
              return (
                item.description !== "" &&
                item.rate !== 0 &&
                item.units !== 0 && (
                  <ul key={i}>
                    <li>{item.description}</li>
                    <li>
                      <span>R</span> {item.rate}
                    </li>
                    <li>{item.units}</li>
                  </ul>
                )
              );
            })}
          </div>
          <div className="total">
            <ul>
              {discountAmount >= 1 && (
                <>
                  <li>DISCOUNT</li>
                  <li>
                    <span>{discountAmount}</span>
                    <span>ZAR</span>{" "}
                  </li>
                </>
              )}
            </ul>
            <ul>
              {storedVat > 0 && (
                <>
                  <li> VAT @ 15%</li>
                  <li>
                    <span>
                      {((totalAmountDue - discountAmount) * storedVat).toFixed(
                        2
                      )}
                    </span>
                    <span>ZAR</span>{" "}
                  </li>
                </>
              )}
            </ul>
            <ul>
              {totalAmountDue > 0 && (
                <>
                  <li> Total Amount Due</li>
                  <li>
                    <span>
                      {(
                        totalAmountDue -
                        discountAmount +
                        (totalAmountDue - discountAmount) * storedVat
                      ).toFixed(2)}
                    </span>
                    <span>ZAR</span>{" "}
                  </li>
                </>
              )}
            </ul>
          </div>
        </div>
        <footer>
          <div className="appriciation">
            <h2>THANK YOU</h2>
            <p>
              <span>for using</span>
              <span>{yourName}</span>
            </p>
          </div>

          <div className="contact">
            <ul>
              <li>Questions? Contact Us!</li>
              <li>
                <span>{email}</span>
                <span>{phone}</span>
              </li>
              <li>
                {yourAddress}, {yourTown}, {yourCity}, {postalCode}
              </li>
            </ul>
            <ul>
              <li>Banking Details</li>
              <li>
                {" "}
                Transfer the amount to the business account below. Please
                include invoice number as reference.
              </li>
              <li>
                <span>Bank</span>
                <span>{bankName}</span>
              </li>
              <li>
                <span>Account No.</span>
                <span>{accountNumber}</span>
              </li>
              <li>
                <span>Branch Code</span>
                <span>{branchCode}</span>
              </li>
            </ul>
          </div>
        </footer>
      </div>
    </>
  );
}
