import React from "react";
import moment from "moment";

import "./PrintInvoice.css";

import arroWRight from "../assets/arrow-right-icon.svg";

export default function Invoice({
  clientName,
  dateIssued,
  invoiceNo,
  yourName,
  yourAddress,
  yourTown,
  yourCity,
  postalCode,
  taxNumber,
  clientVATNumber,
  accountNumber,
  branchCode,
  dateDue,
  email,
  phone,
  website,
  discountAmount,
  items,
  avatar,
  totalAmountVatInclusive,
  totalAmountDue,
  toggleSaveForm,
  saveForm,
  invoiceClass,
  brandColor,
  fontFamily,
  priceType,
  clientTaxNumber,
  clientAddress,
  clientTown,
  clientCity,
  clientPostalCode,
  BankName,
}) {
  const storedVat = sessionStorage.getItem("vat");
  return (
    <>
      <div style={{ fontFamily: fontFamily }} className={invoiceClass}>
        <header>
          <h1>
            <span>
              <img
                src={avatar}
                alt=""
                syle={{ color: "blue", backgroundColor: "blue" }}
              />
            </span>
            <span>Invoice</span>
          </h1>

          <div>
            <ul>
              <li>
                <span>Client</span>
                <span>{clientName}</span>
              </li>
              <li>{clientAddress}</li>
              <li>
                {clientTown !== "" && `${clientTown},`}{" "}
                {clientCity !== "" && `${clientCity},`} {clientPostalCode}
              </li>

              <li>
                {" "}
                <span>Date Issued</span>{" "}
                <span style={{ color: "#323742" }}>
                  {" "}
                  {moment().format("DD MMM YYYY")}
                </span>
              </li>

              <li>
                {" "}
                <span>Invoice No</span>{" "}
                <span style={{ color: "#323742" }}>{invoiceNo}</span>
              </li>
              {clientTaxNumber !== "" && (
                <li>
                  {" "}
                  <span>Tax Reg. No</span>{" "}
                  <span style={{ color: "#323742" }}>{clientTaxNumber}</span>
                </li>
              )}
            </ul>
            <ul>
              <li>
                <span>Supplier</span>
                <span>{yourName}</span>
              </li>
              <li>{yourAddress}</li>
              <li>
                {yourTown !== "" && `${yourTown},`}{" "}
                {yourCity !== "" && `${yourCity},`} {postalCode}
              </li>

              {taxNumber !== "" && (
                <li>
                  {" "}
                  <span>Tax Reg. No:</span>{" "}
                  <span style={{ color: "#323742" }}>{taxNumber}</span>
                </li>
              )}
            </ul>
          </div>
        </header>
        <div className="details">
          <ul>
            <li>Description</li>
            <li>Rate</li>
            <li>{priceType}</li>
            <li>Subtotal</li>
          </ul>
          {items.map((item, i) => {
            return (
              item.description !== "" &&
              item.rate !== 0 &&
              item.units !== 0 && (
                <ul key={i}>
                  <li>{item.description}</li>
                  <li>
                    <span>R</span> {item.rate}
                  </li>
                  <li>{item.units}</li>
                  <li style={{ color: brandColor }}>
                    {" "}
                    <span>R</span> {item.rate * item.units}
                  </li>
                </ul>
              )
            );
          })}
          {discountAmount >= 1 && (
            <ul className="vat">
              <li></li>
              <li></li>
              <li>DISCOUNT</li>
              <li>
                <span>R</span> {discountAmount}
              </li>
            </ul>
          )}
          {storedVat > 0 && (
            <ul className="vat">
              <li></li>
              <li></li>
              <li> VAT @ 15%</li>
              <li>
                <span>R</span>{" "}
                {((totalAmountDue - discountAmount) * storedVat).toFixed(2)}
              </li>
            </ul>
          )}
        </div>
        <footer>
          <div className="total">
            <ul>
              <li>Bank Info</li>
              <li style={{ color: "#f2f6f3" }}>DUE BY</li>
              <li>TOTAL DUE</li>
            </ul>
            <ul>
              <li>
                <span>
                  Bank: <strong style={{ color: "#323742" }}>{BankName}</strong>
                </span>
                <span>
                  Account No:{" "}
                  <strong style={{ color: "#323742" }}>{accountNumber}</strong>
                </span>
                <span>
                  Branch Code:{" "}
                  <strong style={{ color: "#323742" }}>{branchCode}</strong>
                </span>
              </li>
              <li>
                {dateDue !== null &&
                  ` ${moment(dateDue).format("DD MMM ")}'``${moment(
                    dateDue
                  ).format("YY")}`}
              </li>
              <li style={{ color: brandColor }}>
                R
                {(
                  totalAmountDue -
                  discountAmount +
                  (totalAmountDue - discountAmount) * storedVat
                ).toFixed(2)}
              </li>
            </ul>
          </div>

          <div className="contacts">
            <h2>Thank you!</h2>
            <ul>
              {email !== "" && <li>{email}</li>}
              {phone !== "" && (
                <li>
                  <img src={arroWRight} alt="icon" />
                  {phone}
                </li>
              )}
              {website !== "" && (
                <li>
                  <img src={arroWRight} alt="icon" />
                  {website}
                </li>
              )}
            </ul>
          </div>
        </footer>
      </div>
    </>
  );
}
