import React from "react";
import moment from "moment";

import "./UntamedNight.css";

export default function UntamedNight({
  clientName,
  dateIssued,
  invoiceNo,
  yourName,
  yourAddress,
  yourTown,
  yourCity,
  postalCode,
  taxNumber,
  accountNumber,
  branchCode,
  dateDue,
  email,
  phone,
  website,
  discountAmount,
  items,
  avatar,
  totalAmountVatInclusive,
  totalAmountDue,
  toggleSaveForm,
  saveForm,
  invoiceClass,
  brandColor,
  fontFamily,
  bankName,
  priceType,
  clientTaxNumber,
  clientAddress,
  clientTown,
  clientCity,
  clientPostalCode,
}) {
  const storedVat = sessionStorage.getItem("vat");
  return (
    <>
      <div style={{ fontFamily: fontFamily }} className="Untamed-Night">
        <ul className="invoice-number">
          <li>{invoiceNo}</li>
          <li>{moment().format("DD MMM YYYY")}</li>
        </ul>

        <h1>
          <span>
            <img
              src={avatar}
              alt=""
              syle={{ color: "blue", backgroundColor: "blue" }}
            />
          </span>
          <span>Invoice</span>
        </h1>

        <div className="information">
          <div className="Card">
            <h3>Client</h3>
            <ul>
              <li>{clientName}</li>
              <li>{clientAddress}</li>
              <li>
                {clientTown !== "" && `${clientTown},`}{" "}
                {clientCity !== "" && `${clientCity},`} {clientPostalCode}
              </li>

              {clientTaxNumber !== "" && (
                <li>
                  <span>Tax Reg. No: {clientTaxNumber}</span>
                </li>
              )}
            </ul>
          </div>
          <div className="Card">
            <h3>Supplier</h3>
            <ul>
              <li>{yourName}</li>
              <li>{yourAddress}</li>
              <li>
                {yourTown !== "" && `${yourTown},`}{" "}
                {yourCity !== "" && `${yourCity},`} {postalCode}
              </li>

              <li>
                {email !== "" && <span>Email: {email}</span>}
                {phone !== "" && <span>Phone: {phone}</span>}
              </li>
              {taxNumber !== "" && <li>Tax Reg. No: {taxNumber}</li>}
            </ul>
          </div>
          <div className="Card">
            <h3>Date Issued</h3>
            <h4>{moment().format("DD MMM YYYY")}</h4>
          </div>
          <div className="Card">
            <h3>Invoice No.</h3>
            <h4>{invoiceNo}</h4>
          </div>
        </div>

        <div className="details">
          <ul>
            <li>Description</li>
            <li>Rate</li>
            <li>{priceType}</li>
            <li>Subtotal</li>
          </ul>
          <div className="items ">
            {items.map((item, i) => {
              return (
                item.description !== "" &&
                item.rate !== 0 &&
                item.units !== 0 && (
                  <ul key={i}>
                    <li>{item.description}</li>
                    <li>
                      <span>R</span> {item.rate}
                    </li>
                    <li>{item.units}</li>
                    <li style={{ color: brandColor }}>
                      {" "}
                      <span>R</span> {item.rate * item.units}
                    </li>
                  </ul>
                )
              );
            })}
          </div>
          <div className="total">
            <ul>
              {discountAmount >= 1 && (
                <>
                  <li>DISCOUNT</li>
                  <li>
                    <span>{discountAmount}</span>
                    <span>ZAR</span>{" "}
                  </li>
                </>
              )}
            </ul>
            <ul>
              {storedVat > 0 && (
                <>
                  <li> VAT @ 15%</li>
                  <li>
                    <span>
                      {((totalAmountDue - discountAmount) * storedVat).toFixed(
                        2
                      )}
                    </span>
                    <span>ZAR</span>{" "}
                  </li>
                </>
              )}
            </ul>
            <ul>
              {totalAmountDue > 0 && (
                <>
                  <li> Total Amount Due</li>
                  <li>
                    <span>
                      {(
                        totalAmountDue -
                        discountAmount +
                        (totalAmountDue - discountAmount) * storedVat
                      ).toFixed(2)}
                    </span>
                    <span>ZAR</span>{" "}
                  </li>
                </>
              )}
            </ul>
          </div>
        </div>
        <footer>
          <p>
            Transfer the amount to the business account below. Please include
            invoice number as reference.
          </p>
          <ul>
            <li>
              <span></span>
              <span>Bank:</span>
              <span>{bankName}</span>
            </li>
            <li>
              <span></span>
              <span>Account number:</span>
              <span>{accountNumber}</span>
            </li>
            <li>
              <span></span>
              <span>Branch code:</span>
              <span>{branchCode}</span>
            </li>
          </ul>
        </footer>
      </div>
    </>
  );
}
